      if (Month < 2 || Month > 10) {
        // 
        if (Hour < 7 || Hour > 17) {
          this.commitX('changeTheme', 'dark')
        } else {
          this.commitX('changeTheme', 'light')
        }
      } else {
        // 
        if (Hour < 6 || Hour > 19) {
